.ban-slide{
    width: 100%;
    height: fit-content;
    background: url(../../../public/images/bg/slider_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}
.slide-content{
    padding: 50px;
    padding-left: 0;
    overflow: hidden;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.slide-content .slide-title-sm{
    font-size: 48px;
    color: white;
    font-weight: 300;
    text-transform: uppercase;
    margin-bottom: 8px;
    transition: all 900ms linear;
    opacity: 0;
    transition-delay: 0;
    transform: translateY(15px);
}
.swiper-slide-active .slide-content .slide-title-sm{
    opacity: 1;
    transform: translateY(0px);
}
.slide-content .slide-title{
    font-size: 50px;
    line-height: 1.16;
    font-weight: 600;
    text-transform: uppercase;
    color: white;
    margin-bottom: 8px;
    transition: all 900ms linear;
    opacity: 0;
    transition-delay: 450ms;
    transform: translateY(15px);
}
.swiper-slide-active .slide-content .slide-title{
    opacity: 1;
    transform: translateY(0px);
}
.slide-content .slide-desc{
    font-size: 21px;
    line-height: 1.16;
    color: white;
    margin-bottom: 8px;
    transition: all 900ms linear;
    opacity: 0;
    transition-delay: 900ms;
    transform: translateY(15px);
}
.swiper-slide-active .slide-content .slide-desc{
    opacity: 1;
    transform: translateY(0px);
}
.slide-content .thm-btn{
    background: white;
    color: var(--primry-clr);
    transition: all 900ms linear;
    opacity: 0;
    transition-delay: 1350ms;
    transform: translateY(15px);
    margin-top: 10px;
}
.slide-content .thm-btn:hover{
    color: white;
    background: black;
}
.swiper-slide-active .slide-content .thm-btn{
    opacity: 1;
    transform: translateY(0px);
}
.slide-img-box{
    position: relative;
    height: fit-content;
    width: fit-content;
    padding: 21px 10px;
    overflow: hidden;
}
.slide-img-bg{
    width: 100%;
    height: auto;
    border-radius: 50%;
    margin-left: auto;
    box-shadow: 0px 7px 29px 0px rgb(158 53 2 / 5%);
    margin: auto;
    transform: scale(0) translateX(100px);
    transition: all 2100ms;
}
.swiper-slide-active .slide-img-bg{
    transform: scale(1) translateX(0);
}
.slide-img{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    z-index: 2;
    max-width: 85%;
    height: auto;
    transform: translateX(115%);
    transition: all 1500ms;
    transition-delay: 600ms;
}
.swiper-slide-active .slide-img{
    transform: translateX(0);
}